  width: 44px;
  height: 44px;
  justify-content: center;
  align-items: center;
  display: inline-flex;
  position: relative;
}

.social-item-link:hover:before, .social-item-link:focus:before {
  background-color: rgba(232, 232, 226, .2);
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.social-item-link:before {
  content: "";
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  border: 1px solid rgba(232, 232, 226, .5);
  border-radius: 10px;
  position: absolute;
  top: 0;
  left: 0;
